@import url(https://fonts.googleapis.com/css2?family=Staatliches&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: 'Staatliches', cursive;
  font-size: 10px;
}

body {
  background: url(/static/media/mapforappinverted.a977b058.jpg) no-repeat center center fixed;
  background-size: cover;
}

.header__top {
    text-align: center;
    width: 100%;
    height: 10rem;
    padding: 5rem;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    background-color: rgba(10, 10, 10, .6);
}

.header__title {
    color: white;
    font-size: 2.4rem;
    font-weight: normal;
    letter-spacing: .8rem;
    text-transform: uppercase;
}

.main__menu {
    margin-top: 10rem;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.main__button-text {
    text-decoration: none;
    color: #f2f2f2;
    font-size: 3.4rem;
    font-weight: normal;
    letter-spacing: .8rem;
}

.main__button {
    margin: 2rem;
    width: 30rem;
    height: 10rem;
    background-color: rgba(10, 10, 10, .6);
    border-radius: 5rem;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.main__info {
    margin-top: 5rem;
    padding: 2rem 4rem;
    background-color: rgba(10, 10, 10, .6);

}

.main__info-text {
    color: #f2f2f2;
    font-weight: normal;
    font-size: 1.6rem;
    letter-spacing: .8rem;
    line-height: 3rem;
}

.game__image {
  margin-top: 5rem;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.game__countries {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
}

.game__countries h2 {
  color: white;
  font-weight: normal;
  letter-spacing: .8rem;
}

.game__coutnry-name {
  margin: 2rem;
  padding: 3rem;
  background-color: rgba(10, 10, 10, .6);
  border-radius: 2rem;
}

.options {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.options__heading {
    margin: 2rem 0;
    font-size: 3.2rem;
    font-weight: normal;
    letter-spacing: .8rem;
}

label {
    text-align: center;
    font-size: 2.4rem;
    font-weight: bold;
    display: block;
    width: 150px;
}

label::after {
    content: ": "
}

input {
    font-size: 1.6rem;
    display: block;
    margin: 2rem;
    width: 60%;
    padding: .5rem;
    border: 2px solid black;
    border-radius: 2rem;
}

select {
    font-size: 1.6rem;
    margin: 2rem;
    width: 15rem;
    padding: .5rem;
    border: 2px solid black;
    border-radius: 2rem;
}

